<template>
  <ul>
    <li v-for="(list, i) in data" :key="i">
      <a @click="toPeople(i)">
        社区名称{{i}}
        <i class="el-icon-arrow-right" />
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  name: "Community",
  data() {
    return {};
  },
  props: {
    data: null
  },
  mounted() {},
  methods: {
    toPeople(id) {
      //执行父组件的switchComponents方法，并告诉父组件切换到哪个组件，社区的ID是什么
      this.$emit("switchComponent", {
        component: "people",
        id: id
      });
    }
  }
};
</script>